import './node_modules/leaflet-geosearch/dist/geosearch.css';
import 'leaflet-geosearch/dist/geosearch.css'; // Import the CSS
import { GeoSearchControl, OpenStreetMapProvider } from 'leaflet-geosearch';

const BRTA_ATTRIBUTION = 'Kaartgegevens: © <a href="http://www.cbs.nl">CBS</a>, <a href="http://www.kadaster.nl">Kadaster</a>, <a href="http://openstreetmap.org">OpenStreetMap</a><span class="printhide">-auteurs (<a href="http://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>).</span>'
const LUFO_ATTRIBUTION = 'Kaartgegevens: © <a href="http://www.cbs.nl">CBS</a>, <a href="http://www.kadaster.nl">Kadaster</a>, <a href="http://openstreetmap.org">OpenStreetMap</a><span class="printhide">-auteurs (<a href="http://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>).</span>'

const brtRegular = getBaseLayer(BRTA_ATTRIBUTION)
const luchtfoto = getLuchtfoto(LUFO_ATTRIBUTION)

// URL for the WFS service
const geoServerBaseUrl = "https://protoklipenklaar.webgis1.com/geoserver/bodem"
const fieldPointsWfsUrl = geoServerBaseUrl + "/ows?service=WFS&version=1.0.0&request=GetFeature&typeName=bodem%3Afield_centers&outputFormat=application%2Fjson"
const fieldBoundariesWfsUrl = geoServerBaseUrl + "/ows?service=WFS&version=1.0.0&request=GetFeature&typeName=bodem%3Afield_boundaries&outputFormat=application%2Fjson"

// Define the style for the WFS layer
const wfsStyle = {
  color: "red",      // Set the line color to red
  weight: 2,         // Line weight
  opacity: 0         // Line opacity
};

function getBaseLayer (attribution) {
  return L.tileLayer(`https://service.pdok.nl/brt/achtergrondkaart/wmts/v2_0/standaard/EPSG:28992/{z}/{x}/{y}.png`, { // eslint-disable-line no-undef
    WMTS: false,
    attribution: attribution,
    crossOrigin: true
  })
}

function getLuchtfoto  (attribution) {
  return L.tileLayer(`https://service.pdok.nl/hwh/luchtfotorgb/wmts/v1_0/Actueel_orthoHR/EPSG:28992/{z}/{x}/{y}.jpeg`, { // eslint-disable-line no-undef
    WMTS: false,
    attribution: attribution,
    crossOrigin: true
  })
}

var boundaries

// Retrieve WFS data and add it to the map
fetch(fieldBoundariesWfsUrl)
    .then(response => response.json())
    .then(data => {
      // Create a Leaflet GeoJSON layer with the retrieved data and custom style
      boundaries = L.geoJSON(data, {
        style: wfsStyle   // Apply the defined style to the layer
      });

      // Set the minimum zoom for the boundaries layer
      boundaries.options.minZoom = 10;

    })
    .catch(error => {
      console.error("Error fetching WFS data:", error);
    });

let fieldpoints;

// Inside the fetch for fieldPointsWfsUrl
fetch(fieldPointsWfsUrl)
    .then(response => response.json())
    .then(data => {
      // Create a Leaflet GeoJSON layer with the retrieved data and custom style
      const geojsonMarkerOptions = {
        radius: 2,
        fillColor: "#ff800",
        color: "#000",
        weight: 1,
        opacity: 1,
        fillOpacity: 1
      };

      fieldpoints = L.geoJSON(data, {
        onEachFeature: onEachFeature,
        pointToLayer: function(feature, latlng) {
          return L.circleMarker(latlng, geojsonMarkerOptions)
        }
      });

      // Adjusting the marker options
      fieldpoints.eachLayer(function(layer) {
        layer.options.radius = 10;  // Modify the radius, for example
        layer.setStyle({
          radius: 8,
          fillColor: 'red',  // Modify other styles
          color: 'black'
        });
      });

      // Add the fieldpoints layer to the map
      fieldpoints.addTo(map);

    })
    .catch(error => {
      console.error("Error fetching WFS data:", error);
    });

// see "Nederlandse richtlijn tiling" https://www.geonovum.nl/uploads/standards/downloads/nederlandse_richtlijn_tiling_-_versie_1.1.pdf
// Resolution (in pixels per meter) for each zoomlevel
var res = [3440.640, 1720.320, 860.160, 430.080, 215.040, 107.520, 53.760, 26.880, 13.440, 6.720, 3.360, 1.680, 0.840, 0.420]

const RD = new L.Proj.CRS('EPSG:28992', '+proj=sterea +lat_0=52.15616055555555 +lon_0=5.38763888888889 +k=0.9999079 +x_0=155000 +y_0=463000 +ellps=bessel +units=m +towgs84=565.2369,50.0087,465.658,-0.406857330322398,0.350732676542563,-1.8703473836068,4.0812 +no_defs', { // eslint-disable-line no-undef
  transformation: L.Transformation(-1, -1, 0, 0), // eslint-disable-line no-undef
  resolutions: res,
  origin: [-285401.920, 903401.920],
  bounds: L.bounds([-285401.920, 903401.920], [595401.920, 22598.080]) // eslint-disable-line no-undef
});

// Projection parameters for RD projection (EPSG:28992):
const map = L.map('map', { // eslint-disable-line no-undef
  continuousWorld: true,
  crs: new L.Proj.CRS('EPSG:28992', '+proj=sterea +lat_0=52.15616055555555 +lon_0=5.38763888888889 +k=0.9999079 +x_0=155000 +y_0=463000 +ellps=bessel +units=m +towgs84=565.2369,50.0087,465.658,-0.406857330322398,0.350732676542563,-1.8703473836068,4.0812 +no_defs', { // eslint-disable-line no-undef
    transformation: L.Transformation(-1, -1, 0, 0), // eslint-disable-line no-undef
    resolutions: res,
    origin: [-285401.920, 903401.920],
    bounds: L.bounds([-285401.920, 903401.920], [595401.920, 22598.080]) // eslint-disable-line no-undef
  }),
  layers: [
    brtRegular
      ],
  center: [52.176997, 5.200000],
  zoom: 3,
  zoomControl: false
})

var baseLayers = {
  'BRT-Achtergrondkaart [WMTS]': brtRegular,
  'Luchtfoto [WMTS]': luchtfoto
}

new L.Control.Zoom({ position: 'topright' }).addTo(map);

L.control.layers(baseLayers).addTo(map) // eslint-disable-line no-undef

const provider = new OpenStreetMapProvider();

const searchControl = new GeoSearchControl({
  provider: provider, // Use OpenStreetMapProvider instead of pdokProvider
  showMarker: false,
  showPopup: false,
  marker: {
    icon: L.icon({  // Create a custom icon for the marker
      iconUrl: 'path/to/your/icon.png',  // Specify the path to your icon image
      iconSize: [30, 30],  // Adjust the icon size as needed
    }),
    draggable: false,
  },
  // ... (other options)
});

map.addControl(searchControl);

map.on('geosearch/showlocation', function (event) {
  const latlng = event.location;

  // You can now use the latlng to do further operations or set the map view
  map.setView(latlng, 12); // Adjust the zoom level as needed
});

// Listen for the zoomend event on the map
map.on("zoomend", function () {
  const zoomLevel = map.getZoom();

  // Check the current zoom level and adjust visibility of boundaries accordingly
  if (zoomLevel < 10 ) {
    map.removeLayer(boundaries); // Hide the layer
  } else {
    map.addLayer(boundaries); // Show the layer
    fieldpoints.bringToFront();
  }
});

function onmouseover(e) {
  var layer = e.target;
  layer.setStyle({
    fillColor: 'yellow'
  });
}

function resetHighlight(e) {
  var layer = e.target;
  layer.setStyle({
    fillColor: 'red'
  });
}

var pane = map.createPane("fixed", document.getElementById("map"));
function onEachFeature(feature, layer)
{
  layer.veldID = feature.properties.veldID;

  console.log(feature)
  const popup = L.popup({
    pane: "fixed",
    className: "popup-fixed test",
    autoPan: false,
  }).setContent("<h3>Veld: " + feature.properties.veldID + "</h3>" + "<br>Grab the lower right corner and reduce the width of the map. <button>Button</button>");

  // layer.bindPopup("<h3>Second popup 😀</h3><br>Grab the lower right corner and reduce the width of the map.").on("click");

  layer.bindPopup(popup).on("click", fitBoundsPadding);

    layer.on({
    mouseover: function (e) {
      this.setStyle({ fillColor: 'yellow' });
      // Create and open a tooltip with the fieldID
      const tooltip = L.tooltip({
        permanent: true,
        direction: 'top',
        className: 'tooltip',
      }).setContent("Field ID: " + this.veldID);

      this.bindTooltip(tooltip).openTooltip();
    },
    mouseout: function (e) {
      this.setStyle({ fillColor: 'red' });
      // Remove the tooltip on mouseout
      this.unbindTooltip();
    }
  });
}

